import './App.css';
import Home from './components/Home'
import Contact from './components/Contact'
import { createContext, useState } from "react";
import { Routes, Route } from 'react-router-dom';
import HeaderFooter from './components/HeaderFooter';

export const WeatherContext = createContext();
// export const FinalTime = createContext(null);

function App() {

  const [weatherValue, setWeatherValue] = useState('App');
  const [finalTime, setFinalTime] = useState('Blank');

  return (
    <div className={weatherValue}>
      <WeatherContext.Provider value={{weatherValue, setWeatherValue}}>
        {/* <FinalTime.Provider value={(finalTime, setFinalTime)}> */}
          <Routes>
            <Route path ='/' element={<HeaderFooter/>}>
              <Route index element={<Home/>} />
              <Route path='Contact' element={<Contact/>}/>
            </Route>
          </Routes>
        {/* </FinalTime.Provider> */}
      // </WeatherContext.Provider>
    </div>
  );
}

export default App;
